        code: "function sortCharacters(str) {\n  var _a = getSymbols(str),\n      result = _a[0],\n      symObj = _a[1];\n\n  for (var i = 0; i < result.length; i++) {\n    var minIdx = findMin(result.slice(i))[0];\n    minIdx = minIdx + i;\n\n    if (minIdx > i) {\n      result = setLetters(result, minIdx, i);\n    }\n  }\n\n  for (var i = 0; i < str.length; i++) {\n    if (symObj[i]) {\n      result = [result.slice(0, i), result.slice(i)].join(symObj[i]);\n    }\n  }\n\n  return result;\n}\n\nfunction getSymbols(str) {\n  var result = str;\n  var symObj = {};\n\n  for (var i = str.length - 1; i >= 0; i--) {\n    var char = result[i];\n\n    if (!isLetter(char)) {\n      symObj[i] = char;\n      result = [result.slice(0, i), result.slice(i + 1)].join('');\n    }\n  }\n\n  return [result, symObj];\n}\n\nfunction isLetter(a) {\n  var charCode = a.charCodeAt(0);\n\n  if (charCode < 65 || charCode > 122 || charCode > 90 && charCode < 97) {\n    return false;\n  }\n\n  return true;\n}\n\nfunction sortLetters(a, b) {\n  if (!isLetter(a) || !isLetter(b)) {\n    return 100;\n  }\n\n  return a.toLowerCase().charCodeAt(0) - b.toLowerCase().charCodeAt(0);\n}\n\nfunction findMin(str) {\n  var minIdx = -1;\n  var min = str[0];\n\n  for (var i = 1; i < str.length; i++) {\n    if (sortLetters(str[i], min) < 0) {\n      min = str[i];\n      minIdx = i;\n    }\n  }\n\n  return [minIdx, min];\n}\n\nfunction setLetters(str, fromIdx, toIdx) {\n  var result = str;\n  var letter = str[fromIdx];\n  result = [result.slice(0, fromIdx), result.slice(fromIdx + 1)].join('');\n  result = [result.slice(0, toIdx), result.slice(toIdx)].join(letter);\n  return result;\n}\n\nexport default sortCharacters;",
        tscode: "function sortCharacters(str: string): string {\n    let [result, symObj] = getSymbols(str);\n    for (let i = 0; i < result.length; i++) {\n        let [minIdx] = findMin(result.slice(i));\n        minIdx = minIdx + i;\n        if (minIdx > i) {\n            result = setLetters(result, minIdx, i);\n        }\n    }\n    for (let i = 0; i < str.length; i++) {\n        if (symObj[i]) {\n            result = [result.slice(0, i), result.slice(i)].join(symObj[i]);\n        }\n    }\n    return result;\n}\nfunction getSymbols(str: string): [\n    string,\n    Object\n] {\n    let result = str;\n    let symObj = {};\n    for (let i = str.length - 1; i >= 0; i--) {\n        let char = result[i];\n        if (!isLetter(char)) {\n            symObj[i] = char;\n            result = [result.slice(0, i), result.slice(i + 1)].join(\"\");\n        }\n    }\n    return [result, symObj];\n}\nfunction isLetter(a: string): boolean {\n    let charCode = a.charCodeAt(0);\n    if (charCode < 65 || charCode > 122 || (charCode > 90 && charCode < 97)) {\n        return false;\n    }\n    return true;\n}\nfunction sortLetters(a: string, b: string): number {\n    if (!isLetter(a) || !isLetter(b)) {\n        return 100;\n    }\n    return a.toLowerCase().charCodeAt(0) - b.toLowerCase().charCodeAt(0);\n}\nfunction findMin(str: string): [\n    number,\n    string\n] {\n    let minIdx = -1;\n    let min = str[0];\n    for (let i = 1; i < str.length; i++) {\n        if (sortLetters(str[i], min) < 0) {\n            min = str[i];\n            minIdx = i;\n        }\n    }\n    return [minIdx, min];\n}\nfunction setLetters(str: string, fromIdx: number, toIdx: number): string {\n    let result = str;\n    let letter = str[fromIdx];\n    result = [result.slice(0, fromIdx), result.slice(fromIdx + 1)].join(\"\");\n    result = [result.slice(0, toIdx), result.slice(toIdx)].join(letter);\n    return result;\n}\nexport default sortCharacters;\n"
