        comment: {"comment":"/**\n * @description 把M个同样的苹果放在N个同样的盘子里，允许有的盘子空着不放，问共有多少种不同的分法？（用K表示）5，1，1和1，5，1 是同一种分法\n * @param appleCount {Number} 苹果数量\n * @param dishCount {Number} 盘子数量\n * @tag 递归\n * @tag 动态规划\n * @tag 数学\n * @level 略难\n * @returns {Number} 共有多少种分法\n */","meta":{"range":[237,493],"filename":"2rwv7m26fw9ofncgwzecmi.js","lineno":11,"columnno":0,"path":"/tmp","code":{"id":"astnode100000002","name":"putApple","type":"FunctionDeclaration","paramnames":["appleCount","dishCount"]}},"description":"把M个同样的苹果放在N个同样的盘子里，允许有的盘子空着不放，问共有多少种不同的分法？（用K表示）5，1，1和1，5，1 是同一种分法","params":[{"type":{"names":["Number"]},"description":"苹果数量","name":"appleCount"},{"type":{"names":["Number"]},"description":"盘子数量","name":"dishCount"}],"tags":[{"originalTitle":"tag","title":"tag","text":"递归","value":"递归"},{"originalTitle":"tag","title":"tag","text":"动态规划","value":"动态规划"},{"originalTitle":"tag","title":"tag","text":"数学","value":"数学"},{"originalTitle":"level","title":"level","text":"略难","value":"略难"}],"returns":[{"type":{"names":["Number"]},"description":"共有多少种分法"}],"name":"putApple","longname":"putApple","kind":"function","scope":"global"},
        code: "function putApple(appleCount, dishCount) {\n  if (dishCount < 1 || appleCount < 0) {\n    return 0;\n  }\n\n  if (dishCount === 1) {\n    return 1;\n  }\n\n  return putApple(appleCount, dishCount - 1) + putApple(appleCount - dishCount, dishCount);\n}\n\nexport default putApple;",
